    const vhCalc = () => {
        let vh = window.innerHeight * 0.01;
        document.documentElement.style.setProperty('--vh', `${vh}px`);
        console.log('Recalculated VH unit value is ' + vh + 'px');
    };//recalculates vh unit
    vhCalc();
 

    const parallax = (name,offs1,offs2) => {

      if (!offs1) { offs1 = 100; }
      if (!offs2) { offs2 = 100; }
      const selector = '#parallax-' + name;
      if (!($(selector).length)) { return; };

      // css var method (stutter issue)
      // const cssVarName = '--prllx-'+ name +'-y';
      // $(selector).css('object-position','center var(' + cssVarName + ')');
  
      let elemHeight = 0;
      let viewportHeight = 0;
      elemHeight = document.querySelectorAll(selector)[0].getBoundingClientRect().height;
      viewportHeight = Math.max(document.documentElement.clientHeight || 0, window.innerHeight || 0);

      let elemY = document.querySelectorAll(selector)[0].getBoundingClientRect().y;
      posCalc = (elemY - viewportHeight) * -1;
      imageY = offs1 - (posCalc / (elemHeight + viewportHeight) * offs2);
      $(selector).css('object-position','center ' + imageY + '%');

      $(window).scroll(function () {
        elemY = document.querySelectorAll(selector)[0].getBoundingClientRect().y;
        posCalc = (elemY - viewportHeight) * -1;
        imageY = offs1 - (posCalc / (elemHeight + viewportHeight) * offs2);
        $(selector).css('object-position','center ' + imageY + '%');
      });
    }
    // parallax('.parallax',5,5);
